<template>
    <div class="main">
      <p>登录</p>
      <div class="login_content">
        <el-form ref="form" :model="form" label-width="80px">
          <el-input v-model="form.username" placeholder="请输入用户名/手机号" class="input_username" autofocus="true"></el-input>
          <el-input v-model="form.password" placeholder="请输入密码" class="input_password" type="password" @change="passwordChange" @blur="passwordBlur">
          </el-input>
          <div class="input_message">
            <span >{{message}}</span>
          </div>
          <el-button type="primary" @click="onSubmit" class="form_button">登录</el-button>
        </el-form>
      </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      // message: '密码不能少于6位数',
      message: '',
      showMessage: false,
      form: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    onSubmit (button) {
      console.log(button)
      alert(this.form.username + this.form.password)
    },
    passwordChange () {
      this.showMessage = true
    },
    passwordBlur () {
    }
  },
  beforeCreate: {
  }
}
</script>

<style scoped>

.main {
  vertical-align: middle;
  /* box-shadow: 0 0 8px rgba(0,0,0,.1); */
  width: 400px;
  display: inline-block;
  padding: 50px 50px 30px;
  margin: 60px auto 0;
}

.main p {
  color: black;
  font-size: 18px;
}

.login_content .input_username {
  margin-top: 10px;
}

.input_password {
  margin-top: 10px;
}

.input_message {
  color: red;
  text-align: left;
}

.form_button {
  /* margin-top: 50px; */
  position: relative;
  bottom: 10px;
  top: 50px;
  width: 90%;
}

</style>



